import axios from "axios";
import ErrorDetail from "@/components/ErrorDetail";
export default {
  name: "Detail",
  props: ['id'],
  data: function data() {
    return {
      item: {}
    };
  },
  components: {
    ErrorDetail: ErrorDetail
  },
  mounted: function mounted() {
    var _this = this;

    axios.get((window.$elmah_root || '/elmah') + '/api/error?id=' + this.id).then(function (response) {
      _this.item = response.data.error;
    }).catch(function (error) {
      console.log(error);

      _this.$bvToast.toast("Data loading error.", {
        variant: 'danger',
        solid: true,
        noCloseButton: true,
        autoHideDelay: 2000
      });
    });
  }
};